<template>
  <div class="userbar">
    <b-navbar toggleable="lg" type="dark" variant="info">
      <Slide id="slide-menu" v-if="isUserAuthenticated">
        <a id="home" href="#">
          <span>Home</span>
        </a>
      </Slide>
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template #button-content>
            <i class="fa fa-user fa-lg"></i>
          </template>
          <div v-if="isUserAuthenticated">
            <b-dropdown-item href="#">My account</b-dropdown-item>
            <b-dropdown-item @click="signOutButton"
              >Sign out</b-dropdown-item
            >
          </div>
          <div v-else>
              <b-dropdown-item href="/sign_in">Sign in</b-dropdown-item>
              <b-dropdown-item href="/sign_up">Sign up</b-dropdown-item>
            </div>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-navbar>
  </div>
</template>


<script>
import Authentication from "./Authentication";
import User from './User';
import { Slide } from "vue-burger-menu";

export default {
  name: "UserBar",
  components: {
    Slide,
  },
  data() {
    return {
      isUserAuthenticated: null,
    };
  },
  methods: {
    // Sign out by user action
    signOutButton: function () {
      if (Authentication.methods.signOut()) {
        this.$router.push("sign_in");
        this.$notify({
          group: "result",
          type: "success",
          position: "top center",
          duration: 5000,
          title: "Succès",
          text: "A bientôt !",
        });
        this.isUserSignIn = false;
      }
    },
  },
  beforeMount: function() {
    this.isUserAuthenticated = User.methods.isUserAuthenticated();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.userbar {
  font-size: 1rem;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

nav {
  height: 3.5rem;
}

.bm-burger-button {
  position: fixed;
  left: 2rem;
  top: 1rem;
}
</style>