<template>
  <div class="chartmanager">
    <!-- If the user is logged in -->
    <div class="container-fluid" id="main" v-if="isUserAuthenticated">
      <div class="row row-offcanvas row-offcanvas-left">
        <!--/col-->

        <!-- Weekly hours chart -->
        <div class="col main">
          <div class="row mb-3">
            <div class="col-xl-3 col-sm-6 py-2">
              <div class="card h-100">
                <h4 class="card-title mb-0">Weekly hours</h4>
                <div class="card-body pt-2">
                  <bar-chart
                    id="bar"
                    :data="weeklyHours"
                    xkey="day"
                    
                    ykeys='["hours"]'
                    bar-colors='[ "#68D4CD", "#CFF67B", "#94DAFB", "#FD8080", "#889BA3", "#26A0FC", "#6EEEC3" ]'
                    grid="true"
                    grid-text-weight="bold"
                    resize="true" 
                    labels='["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]'
                  >
                  </bar-chart>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-sm-6 py-2">
              <div class="card text-white h-100">
                <div class="card-body">
                  <line-chart
                    id="line"
                    :data="lineData"
                    xkey="year"
                    ykeys='[ "a", "b" ]'
                    labels='[ "Serie A", "Serie B" ]'
                    line-colors='[ "#FF6384", "#36A2EB" ]'
                    grid="true"
                    grid-text-weight="bold"
                    resize="true"
                  >
                  </line-chart>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-sm-6 py-2">
              <div class="card text-white h-100">
                <div class="card-body">
                  <donut-chart
                    id="donut"
                    :data="donutData"
                    colors='[ "#FF6384", "#36A2EB", "#FFCE56" ]'
                    resize="true"
                  >
                  </donut-chart>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-sm-6 py-2">
              <div class="card text-white h-100">
                <div class="card-body">
                  <area-chart
                    id="area_2"
                    data='[
                                { "key": "2013", "value": 30 },
                                { "key": "2014", "value": 25 },
                                { "key": "2015", "value": 29 },
                                { "key": "2016", "value": 50 }
                                ]'
                    resize="true"
                    line-colors='[ "#FF6384", "#36A2EB" ]'
                    grid="true"
                    grid-text-weight="bold"
                  >
                  </area-chart>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--/main col-->
      </div>
    </div>

    <!-- If the user is not logged in -->
    <div v-else>
      <h4 class="mt-3">You are not logged in.</h4>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import config from "../config";

import User from "./User.vue";

// Charts import
import Raphael from "raphael/raphael";
import { BarChart, LineChart, DonutChart, AreaChart } from "vue-morris";
global.Raphael = Raphael;

export default {
  name: "ChartManager",
  components: {
    BarChart,
    LineChart,
    DonutChart,
    AreaChart,
  },
  data() {
    return {
      isUserAuthenticated: false,
      weeklyHours: [
        { day: "Monday", hours: 0 },
        { day: "Tuesday", hours: 0 },
        { day: "Wednesday", hours: 0 },
        { day: "Thirsday", hours: 0 },
        { day: "Friday", hours: 0 },
        { day: "Saturday", hours: 0 },
        { day: "Sunday", hours: 0 },
      ],
      user: null,
      user_datas: null,
      donutData: [
        { label: "Identifiant", value: 300 },
        { label: "Blue", value: 50 },
        { label: "Yellow", value: 100 },
      ],
      lineData: [],
    };
  },
  methods: {
    // Method that loads all datas of the corresponding user, which will be used by charts
    loadUserDatas: async function (id) {
      console.log("loadUserDatas(" + id + ") ...");
      // Load all datas about the Weekly hours chart
      this.loadWeeklyHours(id);
    },
    //Method that loads all datas about the Weekly hours chart
    loadWeeklyHours: function (id) {
      console.log("loadWeeklyHours(" + id + ") ...");

      let weekDebut = this.setToMonday(new Date()).toISOString().split(".")[0];
      let weekEnd = this.setToSunday(new Date()).toISOString().split(".")[0];

      // Get the workingtimes of the current week
      axios
        .get(
          config.API_ROOT +
            "api/workingtimes/" +
            this.user.id +
            "?start=" +
            weekDebut +
            "&end=" +
            weekEnd,
          {
            headers: {
              Authorization: this.user.token,
            },
          }
        )
        .then((response) => {
          console.log(response);
          if (response.status === 200) {
            // Creating the object that will be use by the chart
            response.data.data.forEach((workingtime) => {
              let starting_date = new Date(workingtime.start);
              let ending_date = new Date(workingtime.end);

              if (starting_date.getDate() === ending_date.getDate()) {
                // Adding the working hours to an unique date
                let dayOfWeek = starting_date.getDay() || 7;
                this.weeklyHours[dayOfWeek - 1].hours +=
                  (ending_date - starting_date) / 1000 / 60 / 60;
              } else if (
                ending_date.getDate() - starting_date.getDate() ===
                1
              ) {
                // Adding the working hours to several dates (night work for example)
                let starting_date_end = new Date(
                  starting_date.getFullYear(),
                  starting_date.getMonth(),
                  starting_date.getDate()
                ).setHours(23, 59, 59);
                let ending_date_start = new Date(
                  ending_date.getFullYear(),
                  ending_date.getMonth(),
                  ending_date.getDate()
                ).setHours(0, 0, 0);

                // Adding workingtimes for the "starting" date
                this.weeklyHours[(starting_date.getDay() || 7) - 1].hours +=
                  (starting_date_end - starting_date) / 1000 / 60 / 60;
                console.log(new Date(starting_date_end));
                console.log(
                  "Ajout de " +
                    (starting_date_end - starting_date) / 1000 / 60 / 60 +
                    " heures à " +
                    this.weeklyHours[(starting_date.getDay() || 7) - 1].day
                );
                // Adding workingtimes for the "ending" date
                this.weeklyHours[(ending_date.getDay() || 7) - 1].hours +=
                  (ending_date - ending_date_start) / 1000 / 60 / 60;
              } else {
                // Do nothing because a workingtime during 3 different days is impossible
              }
            });

            this.weeklyHours = [
              /*{
                day: "semaine",
                monday: this.weeklyHours[0].hours,
                tuesday: this.weeklyHours[1].hours,
                wednesday: this.weeklyHours[2].hours,
                thursday: this.weeklyHours[3].hours,
                friday: this.weeklyHours[4].hours,
                saturday: this.weeklyHours[5].hours,
                sunday: this.weeklyHours[6].hours,
              }*/
              this.weeklyHours[0],
              this.weeklyHours[1],
              this.weeklyHours[2],
              this.weeklyHours[3],
              this.weeklyHours[4],
              this.weeklyHours[5],
              this.weeklyHours[6],
            ];
            console.log(this.weeklyHours);
          }
        })
        .catch((error) => {
          console.log(error);
          this.$notify({
            group: "result",
            type: "error",
            position: "top center",
            duration: -1,
            title: "Error",
            text:
              "A communication to the Database failed. Please contact a supervisor.",
          });
        });
    },
    // Get the date of the sunday corresponding to the current week
    setToSunday: function (date) {
      var day = date.getDay() || 7;
      if (day !== 7) date.setHours(24 * (8 - day));
      date.setUTCHours(23, 59, 59, 59);
      return date;
    },
    // Get the date of the monday corresponding to the current week
    setToMonday: function (date) {
      var day = date.getDay() || 7;
      if (day !== 1) date.setHours(-24 * (day - 1));
      date.setUTCHours(0, 0, 0, 0);
      return date;
    },
  },
  beforeMount() {
    // Check if the user is authenticated
    this.isUserAuthenticated = User.methods.isUserAuthenticated();

    // Only if the user is authenticated
    if (this.isUserAuthenticated) {
      // load user datas
      if (this.$route.params.userid != undefined) {
        this.user = { id: this.$route.params.userid };
      } else {
        this.user = JSON.parse(localStorage.user);
      }
      //console.log(this.user);

      // Loading datas about all charts
      this.loadUserDatas(this.user.id);

      this.lineData = [
        { year: "2013", a: 25, b: 55 },
        { year: "2014", a: 94, b: 41 },
        { year: "2015", a: 12, b: 78 },
        { year: "2016", a: 59, b: 13 },
      ];
    }
  },
};
</script>

